"use client"

import { useState, useEffect } from "react"
import { useSearchParams, useRouter } from "next/navigation"
import { ArrowLeft, ArrowRight, Store, Star } from "lucide-react"

interface Seller {
    id: string
    name: string
    businessName: string
    businessDescription: string
    category: string
}

interface Product {
    id: string
    name: string
    price: number
    image: string
    artist: string
}

export default function ChooseSellerPage() {
    const router = useRouter()
    const searchParams = useSearchParams()
    const productId = searchParams.get("productId")

    const [product, setProduct] = useState<Product | null>(null)
    const [sellers, setSellers] = useState<Seller[]>([])
    const [selectedSellerId, setSelectedSellerId] = useState<string | null>(null)
    const [loading, setLoading] = useState(true)

    useEffect(() => {
        const fetchData = async () => {
            setLoading(true)
            try {
                // Fetch Product Details
                if (productId) {
                    const productRes = await fetch(`/api/products/${productId}`)
                    if (productRes.ok) {
                        const data = await productRes.json()
                        setProduct(data.product)
                    }
                }

                // Fetch Sellers List
                const sellersRes = await fetch("/api/seller/list")
                if (sellersRes.ok) {
                    const data = await sellersRes.json()
                    setSellers(data.sellers)
                }
            } catch (error) {
                console.error("Failed to fetch data", error)
            } finally {
                setLoading(false)
            }
        }

        if (productId) {
            fetchData()
        }
    }, [productId])

    const handleProceed = () => {
        if (!selectedSellerId || !productId) return
        router.push(`/checkout?productId=${productId}&sellerId=${selectedSellerId}`)
    }

    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-background">
                <div className="animate-pulse flex flex-col items-center">
                    <div className="h-12 w-12 bg-gray-200 rounded-full mb-4"></div>
                    <p className="text-muted-foreground">Loading sellers...</p>
                </div>
            </div>
        )
    }

    if (!productId || !product) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-background">
                <p className="text-muted-foreground">Product not found.</p>
            </div>
        )
    }

    return (
        <div className="min-h-screen bg-background py-10 px-4">
            <div className="max-w-4xl mx-auto">
                <button
                    onClick={() => router.back()}
                    className="flex items-center text-muted-foreground mb-6 hover:text-primary transition-colors"
                >
                    <ArrowLeft size={16} className="mr-2" />
                    Back to Product
                </button>

                <h1 className="text-3xl font-bold mb-2">Choose a Seller</h1>
                <p className="text-muted-foreground mb-8">
                    Select an artisan to fulfill your order for <strong>{product.name}</strong>.
                </p>

                {/* Product Preview - Optional but nice context */}
                <div className="flex items-center gap-4 bg-muted/30 p-4 rounded-lg border border-border mb-8">
                    <img src={product.image} alt={product.name} className="w-16 h-16 object-cover rounded-md" />
                    <div>
                        <h3 className="font-semibold">{product.name}</h3>
                        <p className="text-primary font-bold">₹{product.price.toLocaleString("en-IN")}</p>
                    </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    {sellers.map((seller) => (
                        <div
                            key={seller.id}
                            onClick={() => setSelectedSellerId(seller.id)}
                            className={`cursor-pointer rounded-xl border p-6 transition-all duration-300 relative ${selectedSellerId === seller.id
                                    ? "border-primary bg-primary/5 ring-1 ring-primary shadow-md"
                                    : "border-border bg-card hover:border-primary/50 hover:shadow-sm"
                                }`}
                        >
                            <div className="flex items-start gap-4">
                                <div className={`p-3 rounded-full ${selectedSellerId === seller.id ? "bg-primary text-primary-foreground" : "bg-muted text-muted-foreground"}`}>
                                    <Store size={24} />
                                </div>
                                <div className="flex-1">
                                    <h3 className="font-bold text-lg mb-1">{seller.businessName}</h3>
                                    <p className="text-sm text-foreground/80 mb-2">{seller.name}</p>
                                    <p className="text-xs text-muted-foreground line-clamp-2 mb-3">{seller.businessDescription}</p>
                                    <span className="inline-block px-2 py-1 bg-secondary text-secondary-foreground rounded text-xs font-medium uppercase tracking-wider">
                                        {seller.category}
                                    </span>
                                </div>
                                {selectedSellerId === seller.id && (
                                    <div className="absolute top-4 right-4 text-primary">
                                        <div className="h-6 w-6 bg-primary rounded-full flex items-center justify-center">
                                            <span className="text-white text-xs">✓</span>
                                        </div>
                                    </div>
                                )}
                            </div>
                        </div>
                    ))}
                </div>

                <div className="mt-10 flex justify-end">
                    <button
                        onClick={handleProceed}
                        disabled={!selectedSellerId}
                        className={`flex items-center px-8 py-3 rounded-lg font-bold transition-all duration-300 ${selectedSellerId
                                ? "bg-primary text-primary-foreground hover:opacity-90 shadow-lg hover:shadow-xl translate-y-0"
                                : "bg-muted text-muted-foreground cursor-not-allowed opacity-50"
                            }`}
                    >
                        Proceed to Checkout
                        <ArrowRight size={20} className="ml-2" />
                    </button>
                </div>
            </div>
        </div>
    )
}
